<template>
  <div v-resize="onResize">
    <v-alert style="position:fixed;z-index:2;right:320px;"
      dismissible
      color="success"
      border="left"
      elevation="4"
      colored-border
      v-model="Alert"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      {{Pesan}}
    </v-alert>

    <div>
      <v-row no-gutters>
        <v-col cols="12" sm="5" md="5" lg="5" v-show="showroster">
          <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Tanggal Roster Cuti</span>
        </v-col>
        <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1" v-show="showroster">
              <v-text-field
                dense
                color="red darken-4"
                @click="rosterdateshow"
                v-model="Roster.Tanggal"
                :append-icon="Roster.Tanggal != null ? 'mdi-calendar-check' : 'mdi-calendar'"
                @click:append="rosterdateshow"
                readonly
              >
                <template v-slot:prepend v-if="mobile == false">
                  <span>:</span>
                </template>
              </v-text-field>
        </v-col>

        <v-col cols="12" sm="5" md="5" lg="5" v-show="showchange">
          <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Tanggal Ganti Roster Cuti</span>
        </v-col>
        <v-col cols="12" sm="7" md="7" lg="7" class="mt-n1" v-show="showchange">
              <v-text-field
                dense
                color="red darken-4"
                v-model="TglGantiRoster.Tanggal"
                @click="rosterdatechange"
                :append-icon="TglGantiRoster.Tanggal != null ? 'mdi-close' : 'mdi-calendar'"
                @click:append="TglGantiRoster.Tanggal != null ? closechange() : rosterdatechange()"
                readonly
              >
                <template v-slot:prepend v-if="mobile == false">
                  <span>:</span>
                </template>
              </v-text-field>
        </v-col>
      </v-row>

    </div>

    <div v-show="mobile == false && isload" class="mt-6">
      <v-btn height="45px" block depressed class="text-capitalize rounded-lg mb-3" color="red darken-2" dark @click="SetRoster"><v-icon left>mdi-send</v-icon><span class="mr-1">Tetapkan Roster Cuti</span></v-btn>
    </div>
    
    <div v-show="mobile == true && isload" class="mt-8">
      <v-btn height="45px" block depressed class="text-capitalize rounded-lg mb-3" color="red darken-2" dark @click="SetRoster"><v-icon left>mdi-send</v-icon><span class="mr-1">Tetapkan Roster Cuti</span></v-btn>
    </div>
    <v-date-picker
        v-show="render"
        ref="datepick"
        no-title
        locale="id-id"
        full-width
        @click:date="ClickDate"
        event-color="green lighten-1"
        :min="AwalTahun" 
        :max="AkhirTahun"
        :events="functionEvents"
      ></v-date-picker>
      <v-date-picker
        v-show="renderganti"
        ref="datepick"
        no-title
        locale="id-id"
        full-width
        @click:date="ClickDateGanti"
        event-color="green lighten-1"
        :min="AwalTahun" 
        :max="AkhirTahun"
        :events="functionEvents"
      ></v-date-picker>
  </div>
</template>
<script>
import api from "@/services/http";
// import Vue from 'vue';
export default {
  data () {
    return {
        mobile:null,
        isload:false,
        render:false,
        renderganti:false,
        showroster:false,
        showchange:false,
        DataRoster:[],
        TglRoster:[],
        TglGanti:[],
        date1: new Date().toISOString().substr(0, 10),
        date2: new Date().toISOString().substr(0, 10),
        windowSize: {x: 0, y: 0},
        date: new Date().toISOString().substr(0, 10),
          notifications: false,
          token:localStorage.getItem('token'),
          MenuTglSelesai: false,
          AwalTahun:new Date().getFullYear()+'-01-01',
          AkhirTahun:new Date().getFullYear()+'-12-31',
          MenuTglMulai: false,
          MenuTglKembaliKerja:false,
          IntervalAlert:'',
          MenuTglPengajuan: false,
          MenuTglGanti: false,
          Roster:{
            Tanggal:null,
          },
          TglGantiRoster:{
            Tanggal:null,
          },
          DefaultRoster:{
            Tanggal:null,
          },
          Alert:false,
          Pesan:''
    }
  },
  async beforeCreate(){
    //
  },

  async mounted(){
    let data = []
     let krywn = JSON.parse(localStorage.getItem('karyawan'))
     let resx = await api.get('getrosterkaryawan/'+krywn.NRK+'?token='+localStorage.getItem('token'))
     resx = resx.data
     resx.forEach(z => {
      // data.push(new Date(z.Tanggal).getDate())
      data.push(z.Tanggal)
     });
      this.TglRoster = data
      this.render = true
      this.isload = true
    this.$refs.datepick.reactive = false
    this.$refs.datepick.landscape = false
    // this.$refs.datepick.nextIcon = null
     console.log(this.$refs.datepick)
    // this.Roster.Tanggal = this.date
  },

  computed: {
//
  },

  watch: {
    windowSize(){
      if (this.windowSize.x < 500) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },
  },

  methods:{ 
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    closechange(){
      this.render = true
      this.renderganti = false
      this.showchange = false
      this.TglGantiRoster.Tanggal = null
    },
    rosterdateshow(){
      this.render = true
      this.renderganti = false
    },
    rosterdatechange(){
      this.render = false
      this.renderganti = true
    },
    async getdata(){
    let data = []
     let krywn = JSON.parse(localStorage.getItem('karyawan'))
        api.get('getrosterkaryawan/'+krywn.NRK+'?token='+this.token).then((res)=>{
            this.DataRoster = res.data
            this.DataRoster.forEach(x => {
              data.push(x.Tanggal)
            });
        })
      this.TglRoster = data
    },
    ClickDate(val){
        this.showroster = true
        if(this.TglRoster.find(x=>x == val)){
          this.showchange = true
          this.Roster.Tanggal = val
          this.render = false
          this.renderganti = true
        }else{
          this.Roster.Tanggal = val
          this.showchange = false
        }
    },
    ClickDateGanti(val){
      if(this.TglRoster.find(x=>x==val)){
        alert('sudah di isi')
      }else{
        this.TglGantiRoster.Tanggal = val
      }
    },
    functionEvents(date) {
      const [, , day] = date.split("-");
      // console.log(day)
    let dax = this.TglRoster.find(x=>x == date)
      const hariminggu = new Date(date).getDay()
      //{ 0 = array hari minggu return tanda merah
      if ([0].includes(parseInt(hariminggu, 10))) return ["red"];
    //  }
    const rosterday = new Date(dax).getDate()
    // console.log('ngorox',rosterday)
    if ([rosterday].includes(parseInt(day, 10))) return ["green"];
    //   if ([10, 11, 12].includes(parseInt(day, 10))) return ["red", "#00f"];
      return false;
    },

    SetRoster(){
      this.$emit('ToastRoster')
      console.log(this.$user)
      this.render = false
      // this.$parent.Toast('Form Cuti Dikirim')
      api.post('/karyawanroster?token='+this.token,{
        NRK:this.$user.NRK,
        Tanggal:this.Roster.Tanggal
        })
        .then((res)=>{
            if(res){
              // this.$router.go(this.$router.currentRoute)
              history.go(0)
              // location.reload()
            }
        })
    },
    SimpanCuti(){
      this.$emit('ToastSimpan')
      api.post('/karyawancuti?token='+this.token,{
        KodeKaryawan: this.$user.KodeKaryawan,
        TanggalPengajuan: this.Cuti.TanggalPengajuan,
        TanggalMulai: this.Cuti.TanggalMulai,
        TanggalSelesai: this.Cuti.TanggalSelesai,
        JamMulai: this.Cuti.JamMulai,
        JamSelesai: this.Cuti.JamSelesai,
        JenisCuti:this.Cuti.JenisCuti,
        AlasanCuti: this.Cuti.AlasanCuti,
        TanggalKerjaKembali:this.Cuti.TanggalKerjaKembali,
        TujuanNotif:null,
        StatusCuti:"Dibuat"
        },{ headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } })
        .then((res)=>{
          if (res) {
            // this.ShowAlert('Simpan')
            this.Cuti = Object.assign({}, this.DefaultCuti)
          } else {
            //
            this.Cuti = Object.assign({}, this.DefaultCuti)
          }
        })
    },
    NotifCutiKaryawan(){
      api.post('/notifikasi?token='+this.token,{
        Kategori: "Approval",
        Judul: "Pengajuan Cuti",
        Konten: this.Cuti.AlasanCuti,
        Attachment:null,
        Dari: this.$user.KodeKaryawan,
        Tujuan: "Human Resources",
        },
        { headers: { 'Content-Type': 'multipart/form-data',"Authorization" : `Bearer ${this.token}` } }
        )
        .then((res)=>{
          if (res) {
            // this.Alert = true
            // console.log('ok lanjut')
          } else {
            //
          }
        })
    },
    ShowAlert(data){
      if (data == 'Simpan') {
        this.Pesan = 'Pengajuan Cuti Di Simpan Ke Draf'
        this.Alert = true
        clearInterval(this.IntervalAlert)
        this.AlertInterval()
      }else if(data == 'Kirim'){
        this.Pesan = 'Pengajuan Cuti Di Kirim Ke HR'
        this.Alert = true
        clearInterval(this.IntervalAlert)
        this.AlertInterval()
      }
    },
    AlertInterval(){
      this.IntervalAlert = setInterval(() => {
        this.Alert = false;
        this.Pesan = '';
      }, 3000)
    },
  }
}
</script>
<style scoped>
/* .v-date-picker-header__value {
  pointer-events: none
} */
.v-date-picker-header {
  display: none
}
</style>