<template>
  <v-container fluid v-resize="onResize" class="scroll-y">
    <div style="max-width: 600px; margin: auto;">
      <v-card flat class="rounded-lg pa-2" :outlined="$vuetify.theme.dark ? true : false" :color="$vuetify.theme.dark ? '' : 'grey lighten-2'">
        <v-sheet outlined class="rounded-lg" :color="$vuetify.theme.dark ? '' : ''">
          <!-- <v-sheet class="mt-1 mb-1">
            <v-layout justify-center>
              <v-chip-group
                v-model="menu_pengajuan_cuti"
                mandatory outlined
              >
                <v-chip
                  :active-class="$vuetify.theme.dark ? 'white black--text' : 'grey darken-4 white--text'"
                  :color="$vuetify.theme.dark ? '' : ''"
                  v-for="(item, i) in items"
                  :key="i+'a'"
                  @click="MenuForm(item.to)"
                >
                  <v-avatar left :color="$vuetify.theme.dark ? '' : ''">
                    <v-icon>{{ item.icon}}</v-icon>
                  </v-avatar>
                  <span v-text="item.text"></span>
                </v-chip>
              </v-chip-group>
            </v-layout>
          </v-sheet>

          <v-divider></v-divider> -->

          <v-toolbar flat height="64px" class="rounded-t-lg" :color="$vuetify.theme.dark ? '' : ''">
            <!-- <v-toolbar-title class="title">
              <span v-show="Menu == 'PengajuanCuti'">Buat Pengajuan Cuti</span>
              <span v-show="Menu == 'DataCuti'">Data Cuti</span>
              <span v-show="Menu == 'DrafCuti'">Draf Cuti</span>
            </v-toolbar-title> -->
            <v-menu max-width="250px" rounded="lg">
              <template v-slot:activator="{ on, attrs }">
                <v-toolbar-title class="title">
                  <span v-bind="attrs" v-on="on" v-show="Menu == 'PengajuanCuti'">Buat Pengajuan Cuti</span>
                  <span v-bind="attrs" v-on="on" v-show="Menu == 'RosterCuti'">Buat Roster Cuti</span>
                  <span v-bind="attrs" v-on="on" v-show="Menu == 'DataCuti'">Data Cuti</span>
                  <span v-bind="attrs" v-on="on" v-show="Menu == 'DrafCuti'">Draf Cuti</span>
                  <v-icon small :color="$vuetify.theme.dark ? 'white' : 'black'"  v-bind="attrs" v-on="on">mdi-chevron-down</v-icon>
                </v-toolbar-title>
              </template>

              <v-list width="220px" class="rounded-lg" :outlined="$vuetify.theme.dark ? true : false">
                <v-list-item-group
                  v-model="selectedItem"
                  mandatory
                  :active-class="$vuetify.theme.dark ? '' : 'grey darken-3 white--text'"
                >
                  <v-list-item
                    v-for="(item, i) in items"
                    :key="i"
                    @click="MenuForm(item.to)"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-text="item.text"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-menu>
          </v-toolbar>

          <v-divider></v-divider>

          <div>
            <PengajuanCuti class="pa-6" v-show="Menu == 'PengajuanCuti'" :Menu="Menu" @ToastKirim="ToastKirim" @ToastSimpan="ToastSimpan"/>
            <RosterCuti class="pa-6" v-show="Menu == 'RosterCuti'" :Menu="Menu" @ToastRoster="ToastRoster"/>
            <DrafCuti v-show="Menu == 'DrafCuti'" :Menu="Menu"/>
            <DataCuti v-show="Menu == 'DataCuti'" :Menu="Menu"/>
          </div>
        </v-sheet>
      </v-card>
    </div>

    <v-fab-transition v-show="mobile == false">
      <v-btn
        :color="$vuetify.theme.dark ? 'grey darken-3' : 'grey darken-3'"
        v-scroll="onScroll"
        v-show="fab"
        fab
        small
        dark
        fixed
        bottom
        right
        @click="toTop"
      >
        <v-icon>mdi-chevron-up</v-icon>
      </v-btn>
    </v-fab-transition>
  </v-container>
</template>
<script>

// const PengajuanCuti = () => import (/* webpackChunkName: "chart-bar"*/ '@/views/FormCuti/PengajuanCuti')
// const DrafCuti = () => import (/* webpackChunkName: "chart-bar"*/ '@/views/FormCuti/DrafCuti')
// const DataCuti = () => import (/* webpackChunkName: "chart-bar"*/ '@/views/FormCuti/DataCuti')
import PengajuanCuti from '@/views/FormCuti/PengajuanCuti'
import DrafCuti from '@/views/FormCuti/DrafCuti'
import DataCuti from '@/views/FormCuti/DataCuti'
import RosterCuti from '@/views/FormCuti/RosterCuti'
  export default {
    components: {
      PengajuanCuti,DrafCuti,DataCuti,RosterCuti
    },
    props: ['FormCutiKaryawan'],
    data () {
      return {
        menu_pengajuan_cuti: 0,
        fab: false,
        MenuPengajuan: false,
        SizeMenuPengajuan: null,
        NavDrawerMenuPengajuan: true,
        mini: true,
        windowSize: {x: 0, y: 0},
        WindowSizeMenuPengajuan: {x: 0, y: 0},
        mobile:null,
        Menu:'RosterCuti',
        selectedItem: 0,
        items: [
          { text: 'Buat Pengajuan Cuti', icon: 'mdi-square-edit-outline', to: 'PengajuanCuti' },
          { text: 'Buat Roster Cuti', icon: 'mdi-content-save-edit-outline', to: 'RosterCuti' },
          { text: 'Data Cuti', icon: 'mdi-text-box-outline', to: 'DataCuti' },
          { text: 'Draf Cuti', icon: 'mdi-content-save-edit-outline', to: 'DrafCuti' },
        ],
      }
    },
    watch:{
      windowSize(){
        if (this.windowSize.x < 600) {
          this.mobile = true
        }else{
          this.mobile = false
        }
      },

      WindowSizeMenuPengajuan(){
        if (this.WindowSizeMenuPengajuan.x < 1300) {
          this.titleClass = "d-none"
          this.SizeMenuPengajuan = true
        }else{
          this.titleClass = "mr-4"
          this.SizeMenuPengajuan = false
        }
      },
    },
    methods:{
      onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 20
      },
      toTop () {
        this.$vuetify.goTo(0)
      },

      wadow(){
        console.log('wkwaokwokw')
      },
      onResize(){
        this.windowSize = { x: window.innerWidth, y: window.innerHeight }
        this.WindowSizeMenuPengajuan = { x: window.innerWidth, y: window.innerHeight }
      },
      ToastRoster(){
        this.$swal.fire({
        toast: true,
        icon: 'success',
        title: 'Roster Cuti Di Tetapkan, Loading Mohon Tunggu',
        animation: false,
        position: 'top-right',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true})
      },
        ToastKirim(){
          this.$swal.fire({
          toast: true,
          icon: 'success',
          title: 'Form Cuti Dikirim',
          animation: false,
          position: 'top-right',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true})
          this.Menu = "DataCuti"
        },
        ToastSimpan(){
          this.$swal.fire({
          toast: true,
          icon: 'success',
          title: 'Form Cuti Disimpan di Draf',
          animation: false,
          position: 'top-right',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true})
          this.Menu = "DrafCuti"
        },
        Tutup(){
        this.$emit('FormCutiKaryawan',false)
        this.Cuti = Object.assign({}, this.DefaultCuti)
        },
        MenuForm(data){
          this.Menu = data
        },
        SimpanCuti(){
          // alert('ok')
        },
        KirimCuti(){
          // alert('kirim')
        }
    }
  }
</script>